<template>
  <v-container>
    <v-row style="padding: 30px 0;" align="center">
      <v-icon color="black" class="icon-padding-right">fa fa-user</v-icon>
      <h1>Edit User</h1>
    </v-row>

    <v-card>
      <v-card-title>
        <v-list dense>
          <v-list-item>{{ getName() }}</v-list-item>
        </v-list>
      </v-card-title>
      <v-card-text>
        <v-list dense>
          <v-form>
            <v-list-item>
              <v-list-item-content>
                <v-text-field
                  v-model="user.forename"
                  label="Forename"
                  required
                ></v-text-field>
              </v-list-item-content>
            </v-list-item>
            <v-list-item>
              <v-list-item-content>
                <v-text-field
                  v-model="user.surname"
                  label="Surname"
                  required
                ></v-text-field>
              </v-list-item-content>
            </v-list-item>
            <v-list-item>
              <v-list-item-content>
                <v-text-field
                  v-model="user.email"
                  label="Email Address"
                  required
                ></v-text-field>
              </v-list-item-content>
            </v-list-item>
            <v-list-item>
              <v-radio-group v-model="user.role" row>
                <v-radio color="#ee6e73" label="Admin" value="admin"></v-radio>
                <v-radio
                  color="#26a69a"
                  label="Landlord"
                  value="landlord"
                ></v-radio>
                <v-radio
                  color="#eea86e"
                  label="Tenant"
                  value="tenant"
                ></v-radio>
              </v-radio-group>
            </v-list-item>
          </v-form>
        </v-list>
      </v-card-text>
      <v-card-actions>
        <v-list-item>
          <v-btn
            style="width: 12%; margin-left:0 !important;"
            class="ma-3"
            @click="goToDashboard"
            color="grey"
            dark
          >
            Back
            <v-icon right>fas fa-chevron-circle-left</v-icon>
          </v-btn>
          <v-btn
            style="width: 12%;"
            class="ma-3"
            @click="deleteUser"
            color="rgb(212, 56, 56)"
            dark
          >
            Delete
            <v-icon right>fas fa-trash</v-icon>
          </v-btn>
          <v-btn
            style="width: 12%;"
            class="ma-3"
            @click="updateUser"
            color="#26a69a"
            dark
          >
            Submit
            <v-icon right>fas fa-paper-plane</v-icon>
          </v-btn>
        </v-list-item>
      </v-card-actions>
    </v-card>
  </v-container>
</template>

<script>
import db from "./firebaseInit";

export default {
  name: "edit-user",
  data() {
    return {
      user: {}
    };
  },
  beforeRouteEnter(to, from, next) {
    db.collection("users")
      .doc(to.params.user_id)
      .get()
      .then(doc => {
        next(vm => {
          vm.user = doc.data();
        });
      });
  },
  methods: {
    updateUser() {
      db.collection("users")
        .doc(this.$route.params.user_id)
        .set(this.user)
        .then(() => this.goToDashboard())
        .catch(err => console.log(err));
    },
    deleteUser() {
      db.collection("users")
        .doc(this.$route.params.user_id)
        .delete()
        .then(() => this.goToDashboard())
        .catch(err => console.log(err));
    },
    goToDashboard() {
      this.$router.push("/dashboard");
    },
    getName() {
      let name = "",
        fname = "",
        sname = "";
      if (this.user.forename !== undefined) fname += this.user.forename;
      if (this.user.surname !== undefined) sname += this.user.surname;
      if (fname.length !== 0) name += fname;
      if (fname.length !== 0 && sname.length !== 0) name += " ";
      if (sname.length !== 0) name += sname;
      if (name === "") return "New User";
      return name;
    }
  }
};
</script>

<style scoped>
.admin {
  background: #ee6e73 !important;
}

.tenant {
  background: #eea86e !important;
}

.landlord {
  background: #26a69a !important;
}

h5 {
  margin: 10px 0;
}

.collection {
  border-radius: 10px;
  border: none;
}

.input-field {
  margin: 18px 50px;
}

input {
  padding: 0 !important;
}

.centralise {
  margin: 0 !important;
  position: absolute !important;
  top: 50% !important;
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}
</style>



