<template>
  <div id="add-user">
    <h3>Add User</h3>

    <ul class="card collection with-header">
      <li class="collection-header">
        <h5>{{ getName() }}</h5>
      </li>
      <li class="collection-item">
        <ul class="collection">
          <div class="input-field">
            <input id="forename" type="text" class="collection-item" v-model="user.forename" value=""/>
            <label for="forename" class="active">Forename</label>
          </div>
          <div class="input-field">
            <input id="surname" type="text" class="collection-item" v-model="user.surname" value=""/>
            <label for="surname" class="active">Surname</label>
          </div>
          <div class="input-field">
            <input id="email" type="text" class="collection-item" v-model="user.email" value=""/>
            <label for="email" class="active">Email Address</label>
          </div>
          <div class="input-field row">
            <p class="col">
              <label>
                <input name="roleRadios" type="radio" value="admin" v-model="user.role" checked />
                <span class="black-text">Admin</span>
              </label>
            </p>
            <p class="col">
              <label>
                <input name="roleRadios" type="radio" value="landlord" v-model="user.role" />
                <span class="black-text">Landlord</span>
              </label>
            </p>
            <p class="col">
              <label>
                <input name="roleRadios" type="radio" value="tenant" v-model="user.role" />
                <span class="black-text">Tenant</span>
              </label>
            </p>
          </div>
        </ul>
      </li>

      <li class="collection-item">
        <button @click="goHome" class="btn waves-effect waves-light back">
          Back
          <i class="fas fa-chevron-circle-left right"></i>
        </button>
        <button @click="addUser" class="btn waves-effect waves-light">
          Submit
          <i class="fas fa-paper-plane right"></i>
        </button>
      </li>
    </ul>
  </div>
</template>

<script>
import db from "./firebaseInit";

export default {
  name: "add-user",
  data() {
    return {
      user: {}
    };
  },
  methods: {
    addUser() {
      db.collection("users")
        .add(this.user)
        .then()
        .catch(err => console.log(err));

      this.goHome();
    },
    goHome() {
      this.$router.push("/");
    },
    getName() {
      let name = '', fname = '', sname = '';
      if (this.user.forename !== undefined) fname += this.user.forename;
      if (this.user.surname !== undefined) sname += this.user.surname;
      if (fname.length !== 0) name += fname;
      if (fname.length !== 0 && sname.length !== 0) name += ' ';
      if (sname.length !== 0) name += sname;
      if (name === '') return 'New User';
      return name;
    }
  }
};
</script>

<style scoped>
.admin {
  background: #ee6e73 !important;
}

.tenant {
  background: #eea86e !important;
}

.landlord {
  background: #26a69a !important;
}

h5 {
  margin: 10px 0;
}

.collection {
  border-radius: 10px;
  border: none;
}

.input-field {
  margin: 18px 50px;
}

input {
  padding: 0 !important;
}

.centralise {
  margin: 0 !important;
  position: absolute !important;
  top: 50% !important;
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}

.back {
  background: gray;
}

.delete {
  background: rgb(212, 56, 56);
}
</style>


