<template>
  <div>
    <v-navigation-drawer v-model="sideNav" color="teal" temporary app dark>
      <v-list dense nav class="py-0">
        <v-list-item two-line>
          <v-list-item-content>
            <v-list-item-title>
              <v-icon style="vertical-align: unset;">fa fa-house-user</v-icon>
              <span style="font-size: 1.5rem;">Rentii</span>
            </v-list-item-title>
          </v-list-item-content>
        </v-list-item>

        <v-divider></v-divider>

        <v-list-item link @click="go('/')">
          <v-list-item-icon>
            <v-icon>fa fa-home</v-icon>
          </v-list-item-icon>

          <v-list-item-content>
            <v-list-item-title>Home</v-list-item-title>
          </v-list-item-content>
        </v-list-item>

        <v-list-item link v-if="isLoggedIn" @click="go('/dashboard')">
          <v-list-item-icon>
            <v-icon>fa fa-tachometer-alt</v-icon>
          </v-list-item-icon>

          <v-list-item-content>
            <v-list-item-title>Dashboard</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>

      <template v-slot:append>
        <div class="pa-2">
          <v-btn block v-if="isLoggedIn" @click="logout">Logout</v-btn>
          <v-btn block v-if="!isLoggedIn" @click="go('/login')">Login</v-btn>
        </div>
      </template>
    </v-navigation-drawer>

    <v-app-bar color="teal" dark app>
      <v-app-bar-nav-icon
        @click="sideNav = !sideNav"
        class="d-flex d-sm-none"
      ></v-app-bar-nav-icon>

      <v-toolbar-title>
        <v-icon style="vertical-align: unset;">fa fa-house-user</v-icon>
        <span style="font-size: 1.5rem;">Rentii</span>
      </v-toolbar-title>
      <v-spacer></v-spacer>

      <v-toolbar-items class="d-none d-sm-flex">
        <v-btn text @click="go('/')">Home</v-btn>

        <v-btn v-if="isLoggedIn" text @click="go('/dashboard')">
          Dashboard
        </v-btn>
        <v-btn v-if="isLoggedIn" text @click="logout">Logout</v-btn>
        <v-btn v-if="!isLoggedIn" text @click="go('/login')">Login</v-btn>
      </v-toolbar-items>
    </v-app-bar>
  </div>
</template>

<script>
import firebase from "firebase";

export default {
  name: "navbar",
  data() {
    return {
      sideNav: false,
      isLoggedIn: false,
      currentUser: false
    };
  },
  created() {
    if (firebase.auth().currentUser) {
      this.isLoggedIn = true;
      this.currentUser = firebase.auth().currentUser.email;
    }
  },
  methods: {
    go(route) {
      this.$router.push(route);
    },
    logout() {
      firebase
        .auth()
        .signOut()
        .then(() => {
          this.$router.go({ path: "/" });
        });
    }
  }
};
</script>

<style scoped>
.icon-padding-right {
  padding-right: 10px;
}
</style>



