<template>
  <v-app id="app">
    <Navbar :key="$route.fullPath" />

    <v-content>
      <router-view></router-view>
    </v-content>

    <v-footer absolute class="font-weight-medium" app>
      <v-col class="text-center" cols="12">
        {{ new Date().getFullYear() }} - <strong>Rentii</strong> - Luca Sett
      </v-col>
    </v-footer>
  </v-app>
</template>

<script>
import Navbar from "@/components/Navbar";
export default {
  name: "app",
  components: {
    Navbar
  }
};
</script>

<style>
main,
header,
footer,
.v-navigation-drawer {
  font-family: "Exo 2" !important;
}

.icon-padding-right {
  padding-right: 10px;
}
</style>



