<template>
  <v-form>
    <v-card-text style="padding-bottom: 0;">
      <v-container>
        <v-text-field
          type="email"
          autocomplete="email"
          v-model="user.email"
          label="Email"
          color="teal"
          :error-messages="emailErrors"
          @input="$v.user.email.$touch()"
          @blur="$v.user.email.$touch()"
        ></v-text-field>

        <v-row>
          <v-col :cols="numCols" style="padding-top: 0; padding-bottom: 0;">
            <v-text-field
              type="password"
              autocomplete="password"
              v-model="password"
              label="Password"
              color="teal"
              :error-messages="passwordErrors"
              @input="$v.password.$touch()"
              @blur="$v.password.$touch()"
            ></v-text-field>
          </v-col>
          <v-col :cols="numCols" style="padding-top: 0; padding-bottom: 0;">
            <v-text-field
              type="password"
              autocomplete="password"
              v-model="passwordConfirm"
              label="Confirm Password"
              color="teal"
              :error-messages="passwordConfirmErrors"
              @input="$v.passwordConfirm.$touch()"
              @blur="$v.passwordConfirm.$touch()"
            ></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col :cols="numCols" style="padding-top: 0; padding-bottom: 0;">
            <v-text-field
              v-model="user.forename"
              label="Forename"
              color="teal"
              :error-messages="forenameErrors"
              @input="$v.user.forename.$touch()"
              @blur="$v.user.forename.$touch()"
            ></v-text-field>
          </v-col>
          <v-col :cols="numCols" style="padding-top: 0; padding-bottom: 0;">
            <v-text-field
              v-model="user.surname"
              label="Surname"
              color="teal"
              :error-messages="surnameErrors"
              @input="$v.user.surname.$touch()"
              @blur="$v.user.surname.$touch()"
            ></v-text-field>
          </v-col>
        </v-row>
        <v-text-field
          v-model="user.telephone"
          label="Mobile Number"
          color="teal"
          :error-messages="telephoneErrors"
          @input="$v.user.telephone.$touch()"
          @blur="$v.user.telephone.$touch()"
        ></v-text-field>
        <v-container style="padding-left: 0; padding-bottom: 0;">
          <v-btn
            @click="register"
            :loading="isLoading"
            :disabled="isLoading"
            color="teal"
            class="white--text"
            type="submit"
            block
          >
            Register
            <v-icon right dark>fa fa-sign-in-alt</v-icon>
          </v-btn>
          <p class="error--text" style="padding-top: 10px;">
            {{ serverMessage }}
          </p>
        </v-container>
      </v-container>
    </v-card-text>
  </v-form>
</template>

<script>
import firebase from "firebase";
import db from "./firebaseInit";

import { validationMixin } from "vuelidate";
import { required, email, sameAs, minLength } from "vuelidate/lib/validators";

export default {
  name: "register",
  mixins: [validationMixin],
  data() {
    return {
      password: "",
      passwordConfirm: "",
      user: { role: "landlord" },
      isLoading: false,
      serverMessage: ""
    };
  },
  methods: {
    register(e) {
      e.preventDefault();
      this.$v.$touch();
      if (!this.$v.$invalid) {
        this.isLoading = true;
        firebase
          .auth()
          .createUserWithEmailAndPassword(this.user.email, this.password)
          .then(
            user => {
              firebase
                .auth()
                .currentUser.sendEmailVerification()
                .then(() => {
                  console.log("Email sent");
                  db.collection("users")
                    .doc(firebase.auth().currentUser.uid)
                    .set(this.user)
                    .then(() => {
                      this.isLoading = false;
                      this.$router.push("/login");
                    })
                    .catch(err => {
                      console.log(err);
                      this.isLoading = false;
                    });
                })
                .catch(err => {
                  this.isLoading = false;
                  console.log(err);
                });
            },
            err => {
              if (err.code === "auth/email-already-in-use")
                this.serverMessage =
                  "There is already an account associated with this email address.";
              else {
                console.log(err);
              }
              this.isLoading = false;
            }
          )
          .catch(err => {
            console.log(err);
            this.isLoading = false;
          });
      }
    },
    resized() {
      console.log("HIIII");
    }
  },
  validations: {
    user: {
      email: { required, email },
      forename: { required },
      surname: { required },
      telephone: { required }
    },
    password: {
      required,
      minLength: minLength(6)
    },
    passwordConfirm: {
      required,
      sameAsPassword: sameAs("password")
    }
  },
  computed: {
    numCols() {
      // return window.innerWidth >= 460 ? "6" : "12";
      switch (this.$vuetify.breakpoint.name) {
        case "xs":
          return "12";
        default:
          return "6";
      }
    },
    emailErrors() {
      const errors = [];
      if (!this.$v.user.email.$dirty) return errors;
      !this.$v.user.email.email && errors.push("Must be a valid email");
      !this.$v.user.email.required && errors.push("Email is required");
      return errors;
    },
    passwordErrors() {
      const errors = [];
      if (!this.$v.password.$dirty) return errors;
      !this.$v.password.required && errors.push("Password is required");
      !this.$v.password.minLength &&
        errors.push("Password must be at least 6 characters in length");
      return errors;
    },
    passwordConfirmErrors() {
      const errors = [];
      if (!this.$v.passwordConfirm.$dirty) return errors;
      !this.$v.passwordConfirm.required &&
        errors.push("Please confirm your password");
      !this.$v.passwordConfirm.sameAsPassword &&
        errors.push("Passwords do not match");
      return errors;
    },
    forenameErrors() {
      const errors = [];
      if (!this.$v.user.forename.$dirty) return errors;
      !this.$v.user.forename.required && errors.push("Forename is required");
      return errors;
    },
    surnameErrors() {
      const errors = [];
      if (!this.$v.user.surname.$dirty) return errors;
      !this.$v.user.surname.required && errors.push("Surname is required");
      return errors;
    },
    telephoneErrors() {
      const errors = [];
      if (!this.$v.user.telephone.$dirty) return errors;
      !this.$v.user.telephone.required &&
        errors.push("Mobile number is required");
      return errors;
    }
  }
};
</script>

<style scoped>
.v-input--selection-controls {
  margin-top: 6px;
}
</style>



