<template>
  <v-container class="fill-height">
    <v-row align="center">
      <v-col>
        <v-card class="mx-auto maxWidth">
          <v-tabs v-model="tab" background-color="teal" dark>
            <v-tab href="#login">Login</v-tab>
            <v-tab href="#register">Register As {{ registerRole }}</v-tab>
          </v-tabs>

          <v-tabs-items v-model="tab">
            <v-tab-item value="register">
              <Register />
            </v-tab-item>

            <v-tab-item value="login">
              <Login />
            </v-tab-item>
          </v-tabs-items>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import Register from "./Register";
import Login from "./Login";

export default {
  name: "login-register-form",
  components: {
    Register,
    Login
  },
  data() {
    return {
      tab: null,
      registerRole: "Landlord"
    };
  }
};
</script>

<style scoped>
@media (min-width: 768px) {
  .maxWidth {
    max-width: 500px;
  }
}

@media (max-width: 767px) {
  .maxWidth {
    max-width: 90% !important;
  }
}
</style>



