<template>
  <v-container>
    <v-row style="padding: 30px 0;">
      <v-icon color="black" class="icon-padding-right"
        >fa fa-tachometer-alt</v-icon
      >
      <h1>Dashboard</h1>
    </v-row>

    <v-row>
      <v-expansion-panels accordion>
        <v-expansion-panel>
          <v-expansion-panel-header>
            <v-row>
              <v-col cols="6">
                <v-row>
                  <v-col cols="1">
                    <v-icon color="black">fa fa-user</v-icon>
                  </v-col>
                  <v-col>
                    <h2>Users</h2>
                  </v-col>
                </v-row>
              </v-col>
            </v-row>
          </v-expansion-panel-header>
          <v-expansion-panel-content>
            <v-list two-line>
              <v-list-item
                v-for="user in users"
                :key="user.id"
                @click="editUser(user.id)"
              >
                <v-list-item-avatar :class="user.role">
                  <span class="white--text" style="font-size: 1.5rem;">
                    {{ user.forename[0] + user.surname[0] }}
                  </span>
                </v-list-item-avatar>
                <v-list-item-content>
                  <v-list-item-title
                    v-text="user.forename + ' ' + user.surname"
                  ></v-list-item-title>
                  <v-list-item-subtitle
                    v-text="formatRole(user.role)"
                  ></v-list-item-subtitle>
                </v-list-item-content>
              </v-list-item>
            </v-list>
          </v-expansion-panel-content>
        </v-expansion-panel>

        <v-expansion-panel>
          <v-expansion-panel-header>
            <v-row>
              <v-col cols="6">
                <v-row>
                  <v-col cols="1">
                    <v-icon color="black">fa fa-home</v-icon>
                  </v-col>
                  <v-col>
                    <h2>Properties</h2>
                  </v-col>
                </v-row>
              </v-col>
            </v-row>
          </v-expansion-panel-header>
          <v-expansion-panel-content>
            <v-list two-line>
              <v-list-item v-for="property in properties" :key="property.id">
                <!-- <v-list-item-avatar :class="user.role">
                  <span class="white--text" style="font-size: 1.5rem;">
                    {{ user.forename[0] + user.surname[0] }}
                  </span>
                </v-list-item-avatar> -->
                <v-list-item-content>
                  <v-list-item-title
                    v-text="property.address1 + ' ' + property.address2"
                  ></v-list-item-title>
                  <!-- <v-list-item-subtitle
                    v-text="
                      property.landlord.forename +
                        ' ' +
                        property.landlord.surname
                    "
                  ></v-list-item-subtitle> -->
                </v-list-item-content>
              </v-list-item>
            </v-list>
          </v-expansion-panel-content>
        </v-expansion-panel>
      </v-expansion-panels>
    </v-row>
  </v-container>
</template>

<script>
import db from "./firebaseInit";
import firebase from "firebase";

export default {
  name: "dashboard",
  data() {
    return {
      users: [],
      properties: [],
      currentUser: {}
    };
  },
  created() {
    // get current user
    db.collection("users")
      .doc(firebase.auth().currentUser.uid)
      .get()
      .then(doc => {
        this.currentUser = doc.data();
        if (this.currentUser.role == "admin") {
          db.collection("users")
            .orderBy("role")
            .get()
            .then(querySnapshot => {
              querySnapshot.forEach(doc => {
                this.users.push({ id: doc.id, ...doc.data() });
              });
            });
          db.collection("properties")
            .get()
            .then(querySnapshot => {
              querySnapshot.forEach(doc => {
                this.properties.push({ id: doc.id, ...doc.data() });
              });
            });
        } else if (this.currentUser.role == "landlord") {
          this.users.push(this.currentUser);
        } else {
          this.users.push(this.currentUser);
        }
      })
      .catch(err => console.log(err));
  },
  methods: {
    formatRole(role) {
      switch (role) {
        case "admin":
          return "Admin";
          break;
        case "tenant":
          return "Tenant";
          break;
        case "landlord":
          return "Landlord";
          break;
        default:
          return "Unknown Role";
          break;
      }
    },
    editUser(id) {
      this.$router.push("/edit_user/" + id);
    }
  }
};
</script>

<style scoped>
* {
  color: black;
}

.admin {
  background: #ee6e73 !important;
}

.tenant {
  background: #eea86e !important;
}

.landlord {
  background: #26a69a !important;
}

h5 {
  margin: 10px 0;
}

.collection {
  border-radius: 10px;
  border: none;
}

.centralise {
  margin: 0 !important;
  position: absolute !important;
  top: 50% !important;
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}

.collectionTitle {
  font-size: 1.64rem;
  line-height: 110%;
}

.collection-header {
  padding: 20px 20px !important;
}

.col {
  padding-top: 0 !important;
  padding-bottom: 0 !important;
}
</style>



